import React, { useContext } from "react";
import "./Intro.css";
import Vector1 from "../../img/Vector1.png";
import Vector2 from "../../img/Vector2.png";
import boy from "../../img/boy.png";
import glassesimoji from "../../img/glassesimoji.png";
import thumbup from "../../img/thumbup.png";
import crown from "../../img/crown.png";
import FloatinDiv from "../FloatingDiv/FloatingDiv";
import Github from "../../img/github.png";
import LinkedIn from "../../img/linkedin.png";
import Instagram from "../../img/instagram.png";
import { themeContext } from "../../Context";
import { motion } from "framer-motion";
import { Link } from "react-scroll";
const Intro = () => {
  // Transition
  const transition = { duration: 2, type: "spring" };

  // context
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  return (
    <div className="Intro" id="Intro">
      {/* left name side */}
      <div className="i-left">
        <div className="i-name">
          {/* yahan change hy darkmode ka */}
          <span style={{ color: darkMode ? "white" : "" }}>Hy! I Am</span>
          <span>Kriti Dwivedi</span>
          <span>
          Nobody is perfect. I am nobody so I am perfect .An enthusiatic Frontend-Developer with intermediate knowledge in back-end as well.
          </span>
        </div>
        <Link to="contact" smooth={true} spy={true}>
          <button className="button i-button">Hire</button>
        </Link>
        {/* social icons */}
        <div className="i-icons">
          <img src={Github} alt="" onClick={() => window.open('https://github.com/KritiDwivedi?tab=repositories')}/>
          <img src={LinkedIn} alt="" onClick={() => window.open('https://www.linkedin.com/in/kriti-dwivedi-819210213')}/>
          <img src={Instagram} alt="" onClick={() => window.open('https://instagram.com')}/>
        </div>
      </div>
      {/* right image side */}
      <div className="i-right">
        <img src={Vector1} alt="" />
        <img src={Vector2} alt="" />
        <img src={boy} alt="" />
        {/* animation */}
        <motion.img
          initial={{ left: "-36%" }}
          whileInView={{ left: "-24%" }}
          transition={transition}
          src={glassesimoji}
          alt=""
        />

        <motion.div
          initial={{ top: "-4%", left: "74%" }}
          whileInView={{ left: "68%" }}
          transition={transition}
          className="floating-div"
        >
          <FloatinDiv img={crown} text1="Web" text2="Development" />
        </motion.div>

        {/* animation */}
        <motion.div
          initial={{ left: "9rem", top: "18rem" }}
          whileInView={{ left: "0rem" }}
          transition={transition}
          className="floating-div"
        >
          {/* floatinDiv mein change hy dark mode ka */}
          <FloatinDiv img={thumbup} text1="Novice" text2="Designer" />
        </motion.div>

        <div className="blur" style={{ background: "#1CD6CE" }}></div>
        <div
          className="blur"
          style={{
            background: " --yellow",
            top: "17rem",
            width: "21rem",
            height: "11rem",
            left: "-20rem",
          }}
        >
          hello
        </div>
      </div>
    </div>
  );
};

export default Intro;
